import React from "react"

import { StaticQuery, graphql } from 'gatsby'

import Layout from "../components/layout"
import SEO from "../components/seo"

import Hero from '../components/Hero/Hero'
import About from '../components/About/About'
import Info from '../components/Info/Info'
import Offer from '../components/Offer/Offer'

export default () =>
  <StaticQuery 
    query={graphql`
      {
        allContentfulDom {
          edges {
            node {
              salon {
                childMarkdownRemark {
                  html
                }
              }
              informacje {
                childMarkdownRemark {
                  html
                }
              }
              oferta {
                childMarkdownRemark {
                  html
                }
              }
              cena {
                childMarkdownRemark {
                  html
                }
              }
            }
          }
        }
      }
    `}
    render={({ allContentfulDom: { edges: [{ node: { salon, informacje, oferta, cena }}]}}) => 
      <Layout>
        <SEO 
          title="Bryś - psi salon fryzjerski"
          description='W tym miejscu Twój pies zobaczy, co to szczęście, a po kilku godzinach pielęgnacji wyjdzie pięknie ostrzyżony, pachnący, a co najważniejsze - szczęśliwy.'
          keywords={[`gatsby`, `application`, `react`]} 
        />
        <Hero />
        <About data={salon.childMarkdownRemark.html} />
        <Info data={informacje.childMarkdownRemark.html} />
        <Offer data={oferta.childMarkdownRemark.html} price={cena.childMarkdownRemark.html} />
      </Layout>
    }
  />
  
