import React from 'react'

import styled from 'styled-components'

export default ({ data }) => 
  <Section>
    <Title>O salonie</Title>
    <Paragraph dangerouslySetInnerHTML={{ __html: data }}/>
  </Section>

const Section = styled.section`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background-color: #E8C268;
  padding: 16px;
`

const Paragraph = styled.p`
  color: #fff;
  font-size: 20px;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  margin: 10px;

  & > *:last-child {
    text-align: center;
  }

  @media(max-width: 1000px) {
    font-size: 18px;
  }
`

const Title = styled.h2`
  color: #8FCADE;
  text-shadow: 0 0 6px rgba(0, 0, 0, .7);
  font-size: 70px;
  margin: 10px;
  text-align: center;

  @media(max-width: 1000px) {
    font-size: 50px;
  }
`